import { Autocomplete, TextField, FormControl, Chip } from "@mui/material";
import { useEffect, useState } from "react";

const HomeSymptomSelector = ({
  symptoms,
  symptomList,
  jsonSymptoms,
  updateFields,
  id,
  label,
  didSubmit
}) => {
  const [didEdit, setDidEdit] = useState(false);
  const [symptomValues, setSymptomValues] = useState([]);

  useEffect(() => {
    if (didSubmit) {
      setSymptomValues([]);
    }
  }, [didSubmit]);

  const filterOptions = (options, { inputValue }) => {
    if (inputValue.length >= 2) {
      return options
        .filter(option => option.toLowerCase().includes(inputValue.toLowerCase()))
        .slice(0, 5);
    }
    return [];
  };

  return (
    <FormControl sx={{ width: "90%" }}>
      <Autocomplete
        multiple
        id={id}
        options={symptomList}
        getOptionLabel={option => option}
        filterOptions={filterOptions}
        value={didEdit ? symptomValues : symptoms.map(s => s.medical_term)}
        renderTags={(value, getTagProps) =>
          value.map((option, index) => (
            <Chip variant="outlined" label={option} {...getTagProps({ index })} />
          ))
        }
        renderInput={params => (
          <TextField {...params} variant="standard" label={label} placeholder="Symptoms" />
        )}
        onChange={(e, newValue) => {
          setDidEdit(true);
          setSymptomValues(newValue);
          const sxObjects = newValue.map(medicalTerm => {
            return jsonSymptoms.find(s => s.medical_term === medicalTerm);
          });
          if (id === "symptoms") {
            updateFields({ symptoms: sxObjects });
          } else if (id === "associated-symptoms") {
            updateFields({ associated_symptoms: sxObjects });
          } else {
            updateFields({ minor: sxObjects });
          }
        }}
      />
    </FormControl>
  );
};
export default HomeSymptomSelector;
