import { useState } from "react";
import TermAutocomplete from "../components/Symptom/TermAutocomplete";
import { Box, Stack, Typography } from "@mui/material";

const flattenHashmap = hashMap => {
  return Object.values(hashMap).flatMap(category => Object.values(category));
};

const Terms = ({ resource }) => {
  const symptoms = resource.read();
  const allTerms = flattenHashmap(symptoms).map(sx => {
    return {
      medical: sx.medical_term,
      symptom: sx.symptom
    };
  });

  const [selectedSymptom, setSelectedSymptom] = useState(null);
  return (
    <Box sx={{ width: "70%", margin: "0 auto", marginTop: "4rem" }}>
      <TermAutocomplete options={allTerms} setSelectedSymptom={setSelectedSymptom} />
      {selectedSymptom && (
        <Stack sx={{ paddingX: "10px", marginTop: "2rem" }} spacing={2}>
          <Typography variant="h6">Layman Term</Typography>
          <Typography variant="body1">{selectedSymptom.symptom}</Typography>
        </Stack>
      )}
    </Box>
  );
};

export default Terms;
