import { Box } from "@mui/material";
import { useEffect, useRef, useState } from "react";
import { Bar } from "react-chartjs-2";
import useCalculator from "../../hooks/useCalculator";

const INITIAL_TAGS = {
  all: 0
};

const TagsBarChart = ({ differentials }) => {
  const chartRef = useRef(null);
  const { getTagCounts } = useCalculator();
  const [tagCounts, setTagCounts] = useState(INITIAL_TAGS);
  const [chartData, setChartData] = useState({
    labels: Object.keys(tagCounts),
    datasets: [
      {
        label: "Tag counts",
        data: Object.values(tagCounts),
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75,192,192,0.7)",
        hoverBorderColor: "rgba(75,192,192,1)"
      }
    ]
  });

  useEffect(() => {
    const differentialTags = getTagCounts(differentials);
    setTagCounts(differentialTags);
    setChartData({
      labels: Object.keys(differentialTags),
      datasets: [
        {
          label: "Tag counts",
          data: Object.values(differentialTags),
          backgroundColor: "rgba(75,192,192,0.4)",
          borderColor: "rgba(75,192,192,1)",
          borderWidth: 1,
          hoverBackgroundColor: "rgba(75,192,192,0.7)",
          hoverBorderColor: "rgba(75,192,192,1)"
        }
      ]
    });

    if (chartRef.current) {
      chartRef.current.update(); // Update the chart data
    }
  }, [differentials]);

  return (
    <Box sx={{ width: "80%", mr: "20px", ml: "50px" }}>
      <Bar
        ref={chartRef}
        data={chartData}
        width={100}
        height={250}
        options={{
          animation: {
            duration: 0 // Set animation duration to 0 for instant appearance
          },
          responsiveAnimationDuration: 0, // Disable responsive animation
          maintainAspectRatio: false,
          scales: {
            y: {
              beginAtZero: true,
              ticks: {
                stepSize: 1
              }
            }
          }
        }}
      />
    </Box>
  );
};
export default TagsBarChart;
