import { Autocomplete, TextField } from "@mui/material";
import { useState } from "react";

const TermAutocomplete = ({ options, setSelectedSymptom }) => {
  const [textInput, setTextInput] = useState("");
  const filterOptions = (options, { inputValue }) => {
    if (inputValue.length >= 2) {
      return options.filter(option =>
        option.medical.toLowerCase().includes(inputValue.toLowerCase())
      );
    }
    return [];
  };

  const isOptionEqualToValue = (option, value) => {
    return option.medical === value.medical && option.symptom === value.symptom;
  };

  return (
    <Autocomplete
      options={options}
      getOptionLabel={option => option.medical}
      noOptionsText={
        textInput.length >= 2 ? "No matching options" : "Enter a search term to see suggestions"
      }
      isOptionEqualToValue={isOptionEqualToValue}
      onChange={(event, newValue) => {
        setSelectedSymptom(newValue);
      }}
      autoHighlight
      filterOptions={filterOptions}
      renderInput={params => (
        <TextField
          {...params}
          label="Medical Term"
          onInput={e => setTextInput(e.target.value)}
          variant="outlined"
          sx={{ width: "70%" }}
        />
      )}
    />
  );
};

export default TermAutocomplete;
