import { Link as RouterLink, useMatch } from "react-router-dom";
import { AppBar, IconButton, Toolbar, Typography, Box, Drawer, useTheme } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import CustomButton from "./CustomButton";
import { useState } from "react";

const Navbar = () => {
  const navLinks = [
    { title: "Differentials", path: "/" },
    { title: "Diagnoses", path: "/diagnoses" },
    { title: "Symptoms", path: "/symptoms" },
    { title: "Add Diagnosis", path: "/diagnoses/add" },
    { title: "Add Symptom", path: "/symptoms/add" },
    { title: "Terms", path: "/terms" }
  ];

  const isLinkActive = path => {
    return useMatch(path);
  };

  const [drawerOpen, setDrawerOpen] = useState(false);
  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  const theme = useTheme();

  const MobileNavList = () => (
    <Box
      sx={{
        width: 250,
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        display: "flex",
        flexDirection: "column"
      }}>
      {navLinks.map(link => (
        <CustomButton
          key={link.title}
          component={RouterLink}
          to={link.path}
          active={isLinkActive(link.path) ? 1 : 0}
          onClick={toggleDrawer}
          sx={{
            flexGrow: 1,
            justifyContent: "center",
            textAlign: "center",
            marginBottom: theme.spacing(2),
            backgroundColor: isLinkActive(link.path) ? "gray" : "white",
            color: "black"
          }}>
          {link.title}
        </CustomButton>
      ))}
    </Box>
  );

  return (
    <Box>
      <AppBar position="static" color="transparent" sx={{ mb: "20px" }}>
        <Toolbar>
          <IconButton
            edge="start"
            aria-label="menu"
            sx={{ display: { xs: "block", sm: "block", md: "none" }, color: "black" }}
            onClick={toggleDrawer}>
            <MenuIcon />
          </IconButton>
          <Typography variant="overline" sx={{ flexGrow: 1, color: "black" }}>
            Symptodoc
          </Typography>
          <Box
            display={{ xs: "none", sm: "none", md: "flex" }}
            sx={{
              flexDirection: "row",
              alignItems: "center"
            }}>
            {navLinks.map(link => (
              <CustomButton
                key={link.title}
                component={RouterLink}
                to={link.path}
                sx={{
                  backgroundColor: isLinkActive(link.path) ? "skyblue" : "white",
                  color: "black"
                }}>
                {link.title}
              </CustomButton>
            ))}
          </Box>
          <Drawer anchor="right" open={drawerOpen} onClose={toggleDrawer}>
            <MobileNavList />
          </Drawer>
        </Toolbar>
      </AppBar>
    </Box>
  );
};

export default Navbar;
